import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

import ReactDOM from 'react-dom';
import { HashRouter, Route, BrowserRouter as Router } from 'react-router-dom';
import './index.css';
import App from './App';
import Apfelkuchen from './Components/Recipes/apfelkuchen';
import CanhChua from './Components/Recipes/canhchua';
import Fruehlingsrollen from './Components/Recipes/fruehlingsrollen';

import * as serviceWorker from './serviceWorker';

const routing = (
    <Router>
        <HashRouter basename="/">
            <div>

                <Route exact path="/" component={App} />
                <Route path="/apfelkuchen" component={Apfelkuchen} />
                <Route path="/canhchua" component={CanhChua} />
                <Route path="/fruehlingsrolle" component={Fruehlingsrollen} />

            </div>
        </HashRouter>

    </Router>
)




ReactDOM.render(routing, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.register();
