import React from 'react';
import '../../css/Details.css';
import { Container, Row, Col, ListGroup } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Link } from 'react-router-dom';

import { faCircle, faClock, faSquare, faArrowLeft, faFlask, faInfoCircle } from '@fortawesome/free-solid-svg-icons';

class Details extends React.Component {
    render() {
        return (
            <div >
                <div class="full">
                    <div className="header-img">
                        <Link to="/"> <span className="navbar-brand mb-0 h1"> <FontAwesomeIcon icon={faArrowLeft} className="back" /></span></Link>
                    </div>
                </div>
                <div class="full">
                    <div className="recipe-description">
                        <Container>
                            <Row>
                                <Col xs={12}>
                                    <p className="header-title">Canh Chua Ca</p>
                                    <p className="text-muted"></p>
                                </Col>

                            </Row>
                            <Row>
                                <Col xs={4}>
                                    <p className="sub-info"> <FontAwesomeIcon icon={faCircle} className="icon-easy" /> easy</p>
                                </Col>
                                <Col xs={4}>
                                    <p className="sub-info"> <FontAwesomeIcon icon={faSquare} className="icon-cake" /> soup</p>
                                </Col>
                                <Col xs={4}>
                                    <p className="sub-info"> <FontAwesomeIcon icon={faClock} className="icon-clock" /> 30min</p>
                                </Col>
                            </Row>
                            <br />
                            <Row>
                                <Col xs={12}>
                                    <p className="header-title">Ingredients <FontAwesomeIcon icon={faFlask} /> </p>
                                </Col>
                            </Row>
                            <Row className="ingredients">
                                <Col xs={12}>
                                    <ListGroup>
                                        <ListGroup.Item>
                                            <Row className="vdivide">
                                                <Col xs={4}>
                                                    <strong>1,5 L</strong>
                                                </Col>
                                                <Col xs={8}>
                                                    Wasser
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                            <Row className="vdivide">
                                                <Col xs={4}>
                                                    <strong>1 EL</strong>
                                                </Col>
                                                <Col xs={8}>
                                                    Salz
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                            <Row className="vdivide">
                                                <Col xs={4}>
                                                    <strong>1 TL</strong>
                                                </Col>
                                                <Col xs={8}>
                                                    Salz
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                            <Row className="vdivide">
                                                <Col xs={4}>
                                                    <strong>3</strong>
                                                </Col>
                                                <Col xs={8}>
                                                    Tomaten
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                            <Row className="vdivide">
                                                <Col xs={4}>
                                                    <strong>1/2</strong>
                                                </Col>
                                                <Col xs={8}>
                                                    Lachs
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                            <Row className="vdivide">
                                                <Col xs={4}>
                                                    <strong>1</strong>
                                                </Col>
                                                <Col xs={8}>
                                                    Schnittlauch
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                            <Row className="vdivide">
                                                <Col xs={4}>
                                                    <strong>1-2 EL</strong>
                                                </Col>
                                                <Col xs={8}>
                                                    Nouc Mam
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                    </ListGroup>
                                </Col>
                            </Row>
                            <br /> <br />
                            <Row>
                                <Col xs={12}>
                                    <p className="header-title">Preparation <FontAwesomeIcon icon={faInfoCircle} /> </p>
                                    <div className="preparation text-muted">
                                        <p>
                                            Wasser mit Salz und Zucker aufkochen lassen. Während dessen die Tomaten in viertel schneiden und in den Topf hinzufügen.
                                        </p>

                                        <p>
                                            Den Fisch in Würfel schneiden. Tomatenschale rausfischen.
                                            Danach den Fisch hinzufügen. Wenn die Suppe ein weiteres Mal aufgekocht hat, Herd runter stellen.
                                        </p>
                                        <p>
                                            Den Schnittlauch in kleinen scheiben schneiden und mit in die Suppe hinzufügen. Zum Schluss noch Nouc Mam zum abschmecken.
                                        </p>



                                    </div>
                                </Col>
                            </Row>
                        </Container>
                    </div>
                </div>
            </div>
        )
    }
}

export default Details;