import React from 'react';
import '../../css/Details.css';
import { Container, Row, Col, ListGroup } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Link } from 'react-router-dom';

import { faCircle, faClock, faSquare, faArrowLeft, faFlask } from '@fortawesome/free-solid-svg-icons';

class Details extends React.Component {
    render() {
        return (
            <div >
                <div class="full">
                    <div className="header-img">
                        <Link to="/"> <span className="navbar-brand mb-0 h1"> <FontAwesomeIcon icon={faArrowLeft} className="back" /></span></Link>
                    </div>
                </div>
                <div class="full">
                    <div className="recipe-description">
                        <Container>
                            <Row>
                                <Col xs={12}>
                                    <p className="header-title">Frühlingsrollen</p>
                                    <p className="text-muted">Fritöse: 176°C | 5min</p>
                                </Col>

                            </Row>
                            <Row>
                                <Col xs={4}>
                                    <p className="sub-info"> <FontAwesomeIcon icon={faCircle} className="icon-easy" /> normal</p>
                                </Col>
                                <Col xs={4}>
                                    <p className="sub-info"> <FontAwesomeIcon icon={faSquare} className="icon-cake" /> snacks</p>
                                </Col>
                                <Col xs={4}>
                                    <p className="sub-info"> <FontAwesomeIcon icon={faClock} className="icon-clock" /> 2h</p>
                                </Col>
                            </Row>
                            <br />
                            <Row>
                                <Col xs={12}>
                                    <p className="header-title">Ingredients <FontAwesomeIcon icon={faFlask} /> </p>
                                </Col>
                            </Row>
                            <Row className="ingredients">
                                <Col xs={12}>
                                    <ListGroup>
                                        <ListGroup.Item>
                                            <Row className="vdivide">
                                                <Col xs={4}>
                                                    <strong>2 kg</strong>
                                                </Col>
                                                <Col xs={8}>
                                                    Karotten
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                            <Row className="vdivide">
                                                <Col xs={4}>
                                                    <strong>2 kg</strong>
                                                </Col>
                                                <Col xs={8}>
                                                    Hackfleisch Schwein
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                            <Row className="vdivide">
                                                <Col xs={4}>
                                                    <strong>1 große</strong>
                                                </Col>
                                                <Col xs={8}>
                                                    Zwiebel
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                            <Row className="vdivide">
                                                <Col xs={4}>
                                                    <strong>1</strong>
                                                </Col>
                                                <Col xs={8}>
                                                    Puree
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                            <Row className="vdivide">
                                                <Col xs={4}>
                                                    <strong>1</strong>
                                                </Col>
                                                <Col xs={8}>
                                                    Eigelb
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                            <Row className="vdivide">
                                                <Col xs={4}>
                                                    <strong>2 TL</strong>
                                                </Col>
                                                <Col xs={8}>
                                                    Salz
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                            <Row className="vdivide">
                                                <Col xs={4}>
                                                    <strong>1 TL</strong>
                                                </Col>
                                                <Col xs={8}>
                                                    Zucker
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                            <Row className="vdivide">
                                                <Col xs={4}>
                                                    <strong>2 TL</strong>
                                                </Col>
                                                <Col xs={8}>
                                                    Pfeffer
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                            <Row className="vdivide">
                                                <Col xs={4}>
                                                    <strong></strong>
                                                </Col>
                                                <Col xs={8}>
                                                    Glasnudeln
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                    </ListGroup>
                                </Col>
                            </Row>
                            <br />

                        </Container>
                    </div>
                </div>
            </div>
        )
    }
}

export default Details;